$neat-grid: (columns: $grid-columns, gutter: $grid-gutter-width);



@for $i from 1 through $grid-columns {
	.col-sm-#{$i} {
		@include grid-column($i);
	}
}


.container {
	@include grid-container;
	margin: auto;
	max-width: 1200px;
}

.row {
	@include clearfix;
}

.col-sm-8 {
	@include grid-column(8);
}

.col-sm-4 {
	@include grid-column(4);
}
